    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.1, 1), background 0.5s cubic-bezier(0.77, 0.2, 0.1, 1), width 0.5s cubic-bezier(0.77, 0.2, 0.1, 1), opacity 0.5s ease, -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.1, 1); }
    .MenuBurger__bar.-one {
